@import '../core/variables.scss';
@import '../core/reset.scss';
.header {
  &__dogList {
    &--bone {
      font-size: 20px;
      margin-left: 10px;
      color: $brandColor-8;
    }
    &--search {
      font-size: 20px;
      color: $brandColor-8;
    }
  }
}

.header2 {
  display: none;
  @media all and (min-width: 768px) {
    display: flex;
    box-shadow: 3px 5px 5px -4px rgba(173, 173, 173, 0.9);
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    margin-left: 20px;
    width: 200px;
  }
  &__others {
    margin-right: 20px;
    &--bone {
      font-size: 40px;
      margin-left: 30px;
      color: $brandColor-8;
      @media all and (min-width: 1024px) {
        display: none;
      }
    }
    &--search {
      font-size: 40px;
      color: $brandColor-3;
    }
  }
}

.dogContainer {
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 25px;
}

.article {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  @media all and (min-width: 768px) {
    justify-content: center;
    align-items: center;
  }
  &__containerImg {
    height: 400px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    display: flex;
    justify-content: center;
    @media all and (min-width: 768px) {
      width: 50%;
    }
  }
  &__image {
    max-height: 400px;
    width: 100%;
    z-index: 0;
    position: relative;
    margin: auto;
    @media all and (min-width: 768px) {
      margin: 0;
      width: auto;
    }
    &--background {
      height: 400px;
      width: calc(95%);
      background-color: #003c5e8e;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
      position: absolute;
      filter: blur(6px);
      z-index: 0;
      @media all and (min-width: 768px) {
        width: 650px;
      }
    }
  }
  &__button {
    transform: translateY(-50px);
    width: 100%;
    height: 45px;
    text-decoration: none;
    color: $brandColor-1;
    font-family: $roboto;
    font-size: 20px;
    font-weight: 600;
    background-color: white;
    border-radius: 23px;
    box-shadow: 5px 9px 15px -4px rgba(173, 173, 173, 0.9);
    border: none;
    border-radius: 25px;
    @media all and (min-width: 768px) {
      margin: auto;
      width: 450px;
    }
  }
}

.asideMain {
  @media all and (min-width: 1024px) {
    display: grid;
    // grid-template-columns: 30%, 70%;
    grid-template-areas: 'aside main';
  }
}
.aside {
  display: none;
  @media all and (min-width: 1024px) {
    //display: flex;
    grid-area: aside;
    grid-row: span 1;
  }
}
.main {
  @media all and (min-width: 1024px) {
    grid-area: main;
  }
}

.nameGender {
  display: flex;
  justify-content: left;
  transform: translate(10px, -50px);
  position: relative;
  z-index: 9;
  &__name {
    font-family: $brandFont;
    font-size: 30px;
    color: white;
  }
  &__gender {
    font-size: 30px;
    margin-top: 10px;
    margin-left: 10px;
  }
}

.prevButton {
  transform: translateY(70px);
}

.mainButtons {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: center;
}

.buttons {
  margin-left: 20px;
  margin-right: 20px;
  transform: translateY(-60px);

  @media all and (min-width: 768px) {
    width: 50%;
    margin: auto;
  }
  &__prev {
    width: 40px;
    height: 40px;
    background-color: white;
    border: none;
    border-radius: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 5px 9px 15px -4px rgba(173, 173, 173, 0.9);
    color: $brandColor-4;
    &--empty {
      width: 40px;
      height: 40px;
      background-color: white;
      border: none;
      border-radius: 75px;
      display: flex;
      justify-content: center;
      align-items: center;
      box-shadow: 5px 9px 15px -4px rgba(173, 173, 173, 0.9);
      color: $brandColor-6;
    }
  }
  &__fav {
    width: 100px;
    height: 100px;
    background-color: white;
    border: none;
    border-radius: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 5px 9px 15px -4px rgba(173, 173, 173, 0.9);
    &--bone {
      height: 40px;
      width: 40px;
      transform: rotate(135deg);
    }
  }
  &__no {
    width: 100px;
    height: 100px;
    background-color: white;
    border-radius: 75px;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 5px 9px 15px -4px rgba(173, 173, 173, 0.9);
    &--cross {
      height: 30px;
      width: 30px;
    }
  }
}
